export default {
  getFormRules (item, type) {
    const rules = []
    if (item.required) {
      rules.push({
        required: item.required,
        message: ''
      })
    }
    if (item.pattern) {
      rules.push({
        pattern: item.pattern,
        message: ' '
      })
    }
    if (item.max && !Number.isNaN(item.max) && typeof item.min === 'undefined') {
      rules.push({
        max: item.max,
        message: `${item.max}`
      })
    }
    if (item.min && !Number.isNaN(item.min) && typeof item.max === 'undefined') {
      rules.push({
        min: item.min,
        message: `${item.min}`
      })
    }
    if (item.min && item.max && !Number.isNaN(item.max) && !Number.isNaN(item.min)) {
      rules.push({
        max: item.max,
        min: item.min,
        message: `${item.min}${item.max}`
      })
    }
    if (type) {
      rules.map((rule) => {
        rule.type = type
        return rule
      })
    }
    return rules
  }
}



