import React, { Component, PropTypes } from 'react'
import './List.scss'
import { Table, Input, Button, Popconfirm } from 'antd'
import MyBreadcrumb from '../../../components/MyBreadcrumb'
const Search = Input.Search

class List extends Component {
  constructor (props) {
    super(props)
    this.state = {
      addBtnType: ['account_info_hotel_list', 'account_info_inner_list',
        'hotel_info_area_list', 'hotel_info_hotel_list']
    }
  }

  componentWillMount () {
    const { type } = this.props.params
    const { setBasicInfo, handleCurrentChange } = this.props
    setBasicInfo(type)
    handleCurrentChange(1)
  }

  componentWillReceiveProps (nextProps) {
    const type = nextProps.params.type
    const { basicInfo } = this.props.List
    if (type !== basicInfo.type) {
      this.props.initData(type)
    }
  }
  goApproveDetail (id, isSubmit) {
    const { type } = this.props.params
    this.context.router.push(`/approve/${type}?id=${id}&isSubmit=${isSubmit}`)
  }

  gotoDetail (id) {
    const { type } = this.props.params
    this.context.router.push(`/detail/${type}?id=${id}`)
  }

  editRow (id) {
    const { type } = this.props.params
    this.context.router.push(`/add/${type}?id=${id}`)
  }

  addRow () {
    const { type } = this.props.params
    this.context.router.push(`/add/${type}`)
  }

  setRowClass (record) {
    if (record['user_status'] === '2') {
      return 'disabled-row'
    }
  }
  render () {
    const { pageInfo, resultInfo, searchInput, loading, basicInfo } = this.props.List
    const { handleCurrentChange, changeSearchInput, deleteRow, configData, disabledRow, payCompleted } = this.props
    const columns = {
      // 
      order_info_kezi_list: [{
        key: 'order_id',
        dataIndex: 'order_id',
        title: ''
      }, {
        key: 'customer_name',
        dataIndex: 'customer_name',
        title: ''
      }, {
        key: 'order_phone',
        dataIndex: 'order_phone',
        title: ''
      }, {
        key: 'order_type',
        dataIndex: 'order_type',
        title: '',
        render: text => {
          const orderType = configData.order_type
          const name = orderType.length ? orderType.filter(item => item.value === text)[0].label : ''
          return <span>{name}</span>
        }
      }, {
        key: 'area_hotel_name',
        dataIndex: 'area_hotel_name',
        title: '/'
      }, {
        key: 'detail',
        dataIndex: 'detail',
        title: '',
        render: (text, record) => {
          const id = record[basicInfo.uniqueKey]
          return (
            <Button
              type="default"
              onClick={() => this.gotoDetail(id)}></Button>
          )
        }
      }],
      // 
      order_info_dajian_list: [{
        key: 'order_id',
        dataIndex: 'order_id',
        title: ''
      }, {
        key: 'customer_name',
        dataIndex: 'customer_name',
        title: ''
      }, {
        key: 'order_phone',
        dataIndex: 'order_phone',
        title: ''
      }, {
        key: 'order_from',
        dataIndex: 'order_from',
        title: ''
      }, {
        key: 'order_area_hotel_type',
        dataIndex: 'order_area_hotel_type',
        title: ''
      }, {
        key: 'area_hotel_name',
        dataIndex: 'area_hotel_name',
        title: '/'
      }, {
        key: 'detail',
        dataIndex: 'detail',
        title: '',
        render: (text, record) => {
          const id = record[basicInfo.uniqueKey]
          return (
            <Button
              type="default"
              onClick={() => this.gotoDetail(id)}></Button>
          )
        }
      }],
      // 
      hotel_info_hotel_list: [{
        key: 'hotel_id',
        dataIndex: 'hotel_id',
        title: ''
      }, {
        key: 'hotel_name',
        dataIndex: 'hotel_name',
        title: ''
      }, {
        key: 'area_list',
        dataIndex: 'area_list',
        title: ''
      }, {
        key: 'hotel_address',
        dataIndex: 'hotel_address',
        title: ''
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderControlBtn(record)
      }],
      // 
      hotel_info_area_list: [{
        key: 'area_id',
        dataIndex: 'area_id',
        title: ''
      }, {
        key: 'area_name',
        dataIndex: 'area_name',
        title: ''
      }, {
        key: 'area_list',
        dataIndex: 'area_list',
        title: ''
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderControlBtn(record)
      }],
      // 
      account_info_register_list: [{
        key: 'user_id',
        dataIndex: 'user_id',
        title: ''
      }, {
        key: 'user_name',
        dataIndex: 'user_name',
        title: ''
      }, {
        key: 'alipay_account',
        dataIndex: 'alipay_account',
        title: ''
      }],
      // 
      feedback_info: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'user_name',
        dataIndex: 'user_name',
        title: ''
      }, {
        key: 'content',
        dataIndex: 'content',
        title: ''
      }, {
        key: 'phone',
        dataIndex: 'phone',
        title: ''
      }],
      // 
      account_info_hotel_list: [{
        key: 'user_id',
        dataIndex: 'user_id',
        title: ''
      }, {
        key: 'user_name',
        dataIndex: 'user_name',
        title: ''
      }, {
        key: 'hotel_name',
        dataIndex: 'hotel_name',
        title: ''
      }, {
        key: 'hotel_area',
        dataIndex: 'hotel_area',
        title: ''
      }, {
        key: 'user_status',
        dataIndex: 'user_status',
        title: '',
        render: (text) => <span>{text === '1' ? '' : ''}</span>
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderControlBtn(record, true)
      }],
      // 
      account_info_inner_list: [{
        key: 'user_id',
        dataIndex: 'user_id',
        title: ''
      }, {
        key: 'user_name',
        dataIndex: 'user_name',
        title: ''
      }, {
        key: 'user_type',
        dataIndex: 'user_type',
        title: ''
      }, {
        key: 'user_status',
        dataIndex: 'user_status',
        title: '',
        render: (text) => <span>{text === '1' ? '' : ''}</span>
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderControlBtn(record, true)
      }],
      // 
      finance_info_kezi_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'order_other_money',
        dataIndex: 'order_other_money',
        title: ''
      }, {
        key: 'sign_pic_count',
        dataIndex: 'sign_pic_count',
        title: '',
        render: text => <span>{text}</span>
      }, {
        key: 'sign_status',
        dataIndex: 'sign_status',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '0':
              data = ''
              break
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderApproveControl(record)
      }],
      // 
      finance_info_dajian_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'sign_type',
        dataIndex: 'sign_type',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
            case '4':
              data = ''
              break
            case '5':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'user_type',
        dataIndex: 'user_type',
        title: '',
        render: (text, record) => <span>{record.sign_type === '1' ? '' : ''}</span>
      }, {
        key: 'sign_pic_count',
        dataIndex: 'sign_pic_count',
        title: '',
        render: text => <span>{text}</span>
      }, {
        key: 'sign_status',
        dataIndex: 'sign_status',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '0':
              data = ''
              break
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderApproveControl(record)
      }],
      // 
      manager_info_kezi_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'order_other_money',
        dataIndex: 'order_other_money',
        title: ''
      }, {
        key: 'sign_pic_count',
        dataIndex: 'sign_pic_count',
        title: '',
        render: text => <span>{text}</span>
      }, {
        key: 'boss_sign_status',
        dataIndex: 'boss_sign_status',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '0':
              data = ''
              break
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderApproveControl(record)
      }],
      // 
      manager_info_dajian_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'sign_type',
        dataIndex: 'sign_type',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
            case '4':
              data = ''
              break
            case '5':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'user_type',
        dataIndex: 'user_type',
        title: '',
        render: (text, record) => <span>{record.sign_type === '1' ? '' : ''}</span>
      }, {
        key: 'sign_pic_count',
        dataIndex: 'sign_pic_count',
        title: '',
        render: text => <span>{text}</span>
      }, {
        key: 'boss_sign_status',
        dataIndex: 'boss_sign_status',
        title: '',
        render: text => {
          let data = ''
          switch (text) {
            case '0':
              data = ''
              break
            case '1':
              data = ''
              break
            case '2':
              data = ''
              break
            case '3':
              data = ''
              break
          }
          return <span>{data}</span>
        }
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderApproveControl(record)
      }],
      // --
      remittance_info_kezi_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'order_other_money',
        dataIndex: 'order_other_money',
        title: ''
      }, {
        key: 'create_user_name',
        dataIndex: 'create_user_name',
        title: ''
      }, {
        key: 'create_user_money',
        dataIndex: 'create_user_money',
        title: ''
      }, {
        key: 'watch_user_name',
        dataIndex: 'watch_user_name',
        title: ''
      }, {
        key: 'watch_user_money',
        dataIndex: 'watch_user_money',
        title: ''
      }, {
        key: 'pay_status',
        dataIndex: 'pay_status',
        title: '',
        render: text => <span>{text === '4' ? '' : ''}</span>
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderPayControl(record)
      }],
      // --
      remittance_info_dajian_contract: [{
        key: 'id',
        dataIndex: 'id',
        title: ''
      }, {
        key: 'order_money',
        dataIndex: 'order_money',
        title: ''
      }, {
        key: 'first_order_money',
        dataIndex: 'first_order_money',
        title: ''
      }, {
        key: 'create_user_name',
        dataIndex: 'create_user_name',
        title: ''
      }, {
        key: 'create_user_money',
        dataIndex: 'create_user_money',
        title: ''
      }, {
        key: 'pay_status',
        dataIndex: 'pay_status',
        title: '',
        render: text => <span>{text === '4' ? '' : ''}</span>
      }, {
        key: 'control',
        dataIndex: 'control',
        title: '',
        render: (text, record) => renderPayControl(record)
      }]
    }
    const renderControlBtn = (record, showDisabled) => {
      const id = record[basicInfo.uniqueKey]
      const dataStatus = record['user_status']
      return (
        <div>
          { showDisabled &&
          <Popconfirm
            title={`${dataStatus === '1' ? '' : ''}?`}
            onConfirm={() => disabledRow(id, dataStatus)}>
            <Button type="primary"
              icon={dataStatus === '1' ? 'unlock' : 'lock'} ghost shape="circle" style={{ marginRight: 5 }} />
          </Popconfirm>
          }
          <Button type="primary" icon="edit" ghost shape="circle" style={{ marginRight: 5 }}
            onClick={() => this.editRow(id)} />
          <Popconfirm title="?" onConfirm={() => deleteRow(id)}>
            <Button type="danger" icon="delete" ghost shape="circle" />
          </Popconfirm>
        </div>
      )
    }
    const renderApproveControl = (record) => {
      return (
        <div>
          <Button
            size="small"
            onClick={() => this.goApproveDetail(record.id, false)}></Button>
          <Button
            type="primary"
            size="small"
            style={{ marginLeft: 5 }}
            onClick={() => this.goApproveDetail(record.id, true)}>
            { record.sign_status === '3' ? '' : '' }
          </Button>
        </div>
      )
    }
    const renderPayControl = (record) => {
      return (
        <div>
          <Button size="small" style={{ marginRight: 5 }} onClick={() => this.gotoDetail(record.id)}></Button>
          {record.pay_status === '4' ? (
            <span className="completed-pay"></span>
          ) : (
            <Popconfirm title="?" onConfirm={() => payCompleted(record)}>
              <Button type="primary" size="small"></Button>
            </Popconfirm>
          )}
        </div>
      )
    }

    const pagination = {
      current: pageInfo.page,
      total: resultInfo.count,
      showQuickJumper: true,
      pageSize: pageInfo.pagesize,
      showTotal (total) {
        return ` ${total} `
      }
    }

    return (
      <div className="list-page">
        <MyBreadcrumb breadcrumb={basicInfo.breadcrumb} />
        <div className="control-box clearfix">
          <Search
            placeholder="/"
            style={{ width: 200, display: basicInfo.type === 'account_info_hotel_list' ? 'block' : 'none' }}
            value={searchInput}
            onChange={(e) => changeSearchInput(e.target.value)}
            onSearch={() => handleCurrentChange(1)}
          />
          <Button
            type="primary"
            icon="plus-circle-o"
            className="create-btn"
            style={{ display: this.state.addBtnType.indexOf(basicInfo.type) > -1 ? 'block' : 'none' }}
            onClick={() => this.addRow()}></Button>
        </div>
        <Table
          loading={loading}
          rowKey={record => record[basicInfo.uniqueKey]}
          pagination={pagination}
          onChange={(pageInfo) => handleCurrentChange(pageInfo.current)}
          dataSource={resultInfo.list}
          rowClassName={(record) => this.setRowClass(record)}
          columns={columns[basicInfo.type]} />
      </div>
    )
  }
}

List.propTypes = {
  disabledRow: PropTypes.func,
  payCompleted: PropTypes.func,
  configData: PropTypes.object,
  deleteRow: PropTypes.func,
  initData: PropTypes.func,
  changeSearchInput: PropTypes.func,
  handleCurrentChange: PropTypes.func,
  setBasicInfo: PropTypes.func,
  List: PropTypes.object,
  breadcrumb: PropTypes.array,
  dataSource: PropTypes.array,
  columns: PropTypes.array,
  params: PropTypes.object,
  type: PropTypes.string
}

List.contextTypes = {
  router: React.PropTypes.object.isRequired
}

export default List



